.fast-review {
	background-color: #DFDDDD;
	padding: 20px 20px 30px;
	width: 100%;
	margin-bottom: 10px;
	.popup__subtitle {
	 display: block;
	 padding-bottom: 20px; }
	&__line {
	 display: flex; }
	.form-group {
	 display: flex;
	 flex-direction: row;
	 align-items: center;
		.form-input__field {
		 margin-right: 10px; }
		&:last-child {
		 margin-right: 0; } }
	.form-line__button {
	 padding: 14px 25px;
	 min-width: 125px; } }




.review-line {
	position: absolute;
	height: 100%;
	width: 0;
	left: 1px;
	z-index: 1;
	background-color: #F4E10A;
	transition: all 0.6s ease;
	&__wrapper {
	 margin: 30px 10px 50px;
	 position: relative; }
	&__inner {
	 display: flex;
	 justify-content: space-between;
	 height: 5px;
	 background-color: #ededed;
	 align-items: flex-end;
	 cursor: pointer; }
	&__item {
	 height: 20px;
	 border-left: 1px solid #ededed;
	 margin-bottom: -6px;
	 position: relative;
	 transition: all 0.5s ease;
	 &:before {
	  content: attr(data-fast-review);
	  display: block;
	  font-size: 12px;
	  color: #8E8989;
	  position: absolute;
	  top: -20px;
	  left: 0;
	  white-space: nowrap;
	  transition: all 0.4s ease; }
	 &.review-line__item {
	  &--center {
	   &:before {
		   left: -50%;
		   transform: translateX(-50%); } }
		 &--right {
		  &:before {
		   left: auto;
		   right: 0; } } }
		&--active {
		 border-left: 1px solid #E3E60C;
		 .review-line__number {
		  background-color: #E3E60C; }
		 &.review-line__item:before {
		  color: #4A4747; } } }
	&__number {
	 font-size: 14px;
	 line-height: 24px;
	 width: 24px;
	 height: 24px;
	 text-align: center;
	 position: absolute;
	 top: 100%;
	 left: -12px;
	 border-radius: 50%;
	 cursor: pointer;
	 transition: all 0.4s ease;
	 &:hover {
	  background-color: #fff; } } }
